import React, { Component } from 'react'
import { Link } from 'react-router-dom';

class searchDiv extends Component {
  constructor(props) {
    super(props)
    this.state = {
      user: this.props.user,
      userMail: this.props.user.email,
      userName: this.props.username,
      accounts: this.props.accounts,
      account: this.props.account
    }
  }

  render() {
    var accounts = this.state.accounts;
    var following = "";
    if(this.state.account.following){
        this.state.account.following.forEach(element => {
            following += element+"/";
        });
    }
    var usernameMem = this.state.account.username;
    return (
      <div className="searchContent">
        <div className="searchHeader">
            <div className="searchbar">
                <input className="search_input" type="text" name="" placeholder="Search ..." />
            </div>
        </div>
        <div className="searchBody">
            {
                Object.keys(this.state.accounts).map(function(key) {
                    var cont=0;
                    cont++;
                    if(accounts[key].username == usernameMem){
                        return ;
                    }else{
                        return (
                            <div className="objectProfileSearch">
                                <div className="imageProfileSearch">
                                    <img src="images/profile1.jpg" />
                                </div>
                                <div className="describeProfileSearch">
                                    <Link to={"/profile/"+accounts[key].username}>
                                        {accounts[key].username}
                                    </Link>
                                </div>
                                <div className="followBtnSearch">
                                    {
                                    following.search(accounts[key].id)<0?
                                        ( <input type="submit" className="btn btn-success followSearhBtn" value="Seguir"></input> )
                                    :
                                        ( <input type="submit" className="btn btn-warning followSearhBtn" value="Dejar de seguir"></input> )
                                    }                                
                                </div>
                            </div>
                        )
                    }
                })
            }
            <hr/>
        </div>
        <div className="bottomMenu">
            <Link to="/home">
                <div className="buttonBottom">
                    <p> 
                        <img src="icons/general/homeicon.png" /> 
                    </p>
                </div>
            </Link>
            <Link to="/notifications">
                <div className="buttonBottom">
                    <p> 
                        <img src="icons/general/favoriteicon.png" /> 
                    </p>
            </div>
            </Link>
            <Link to="/search">
                <div className="buttonBottom">
                    <p> 
                        <img src="icons/general/searchicon.png" /> 
                    </p>
                </div>
            </Link>
            <Link to={"/profile/"+this.state.userName}>
                <div className="buttonBottom">
                    <p> 
                        <img src="icons/general/profileicon.png" /> 
                    </p>
                </div>
            </Link>
        </div>
      </div>
    )
  }
}

export default searchDiv;
